.footer {
    height: 200px;
    width: 100%;
    color: white;
    background-color: #000000;
}

.footerContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.items {
    display: flex;
    width: 300px;
    justify-content: space-between;
    flex-wrap: wrap;
}

.item {
    width: 50px;
    height: 50px;
    background-color: #4e8181;
}
